import {
  animation, trigger, animateChild, group,
  transition, animate, style, query
} from '@angular/animations';

export const fader =
  trigger('routeAnimations', [
    transition('* <=> *', [
      style({ position: 'relative' }),
      query(':enter, :leave', [
        style({
          position: 'absolute',
          opacity: 0,
          left: 0,
          width: '100%'
        })
      ]),
      query(':enter', [
        animate('300ms ease',
        style({ opacity: 1})
        ),
      ]),
    ])
  ]);
