import { NgModule } from '@angular/core';
import { Routes, RouterModule, ExtraOptions } from '@angular/router';


import { InicioComponent } from './inicio/inicio.component';
import { MetodologiaComponent } from './metodologia/metodologia.component';
import { ProyectosComponent } from './proyectos/proyectos.component';
import { InfoComponent } from './info/info.component';

const routerOptions: ExtraOptions = {
  scrollPositionRestoration: 'enabled',
  onSameUrlNavigation: 'reload',
  anchorScrolling: 'enabled',
  scrollOffset: [0, 80]
};

const routes: Routes = [
  { path: '', component: InicioComponent, data: {animation: 'fader'} },
  { path: 'metodologia', component: MetodologiaComponent, data: {animation: ''} },
  { path: 'proyectos', component: ProyectosComponent, data: {animation: ''} },
  { path: 'info', component: InfoComponent, data: {animation: '*'} }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, routerOptions)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
